import React from 'react';

class TableMatiere extends React.Component  {

    componentDidMount(){
        const fiche = document.getElementById("ficheCours__tableMatiere");
        if(!fiche){
            return;
        }
        const tableMatiere = document.getElementById("tableMatiere");
        if(!tableMatiere){
            return;
        }
        window.onscroll = function() {
            if (window.pageYOffset>fiche.offsetTop) {
                tableMatiere.classList.add("sticky");
            } else {
                tableMatiere.classList.remove("sticky");
            }
        };
    }

    componentWillUnmount(){
        window.onscroll = null;
    }

    /**
     * Rendu de la page HTML
     */
    render(){
        if(this.props.contents.length === 0){
            return null;
        }
        return (
            <div id="tableMatiere" className="tableMatiere">
                <h4 className="label">Table des matières</h4>
                {this.props.contents.map((section)=>(
                    <div key={"c"+section.id}>
                        <h4 className="principal">
                            <a href={`#${section.id}`}>
                                {section.titre}
                            </a>
                        </h4>
                        {section.subSections.map((sub,i)=>(
                            <h4 key={sub.titre+i} className="sub">
                                <a href={`#${sub.id}`}>{sub.titre}</a>
                            </h4>
                        ))}
                    </div>
                ))}
            </div>  
        );
    }

}

export default TableMatiere;