const initialState = {
    userConnected: {},
    authenError : false,
    logged: false,
    logging: false,
    loadingMe : true,
    isOpenAuth : false
}

export default function reducer(state = initialState, action = {}) {

    switch (action.type) {

        case "SET_USER_CONNECTED":
            return {...state,userConnected: action.user}

        case "AUTHEN_ERROR":
            return{...state,authenError : action.error}

        case "LOGGED":
            return{...state,logged : action.logged}

        case "LOGGING":
            return{...state,logging : action.logging}

        case "LOADING_ME":
            return {...state, loadingMe : action.loading}

        case "TOGGLE_AUTH" : 
            return {...state, isOpenAuth : !state.isOpenAuth}
        default:
            return state;
    }
}

export const isLogging = (state)=>(state.userReducer.logging);
export const getUserConnected = (state)=>(state.userReducer.userConnected);
export const getToken = (state)=>(state.userReducer.userConnected.token);

export const loadingMe = (state)=>(state.userReducer.loadingMe);
export const isAuthenError = (state)=>(state.userReducer.authenError);

export const isOpenAuth = (state)=>(state.userReducer.isOpenAuth);