import React from 'react';
import TextRender from '../../../../commun/TextRender';
import TableMatiere from './TableMatiere';
import ProtectedButton from '../../../../commun/ProtectedButton';
import { toLink } from '../../../../navigation';
import { toRoman } from '../../../../utils/toroman';
import { lienGestionChapitreContent } from '../../../../navigation/cours';

class Chapitre extends React.Component  {

    editerContent = ()=>{
        toLink(lienGestionChapitreContent(this.props.chapitre.id));
    }

    /**
     * Rendu de la page HTML
     */
    render(){
        return (
            <div className="ficheCours">
                <div className="ficheCours__content">
                    <div className="text-right d-block">
                        <ProtectedButton 
                            onClick = {this.editerContent} 
                            title="Modifier le cours" 
                            className="btn btn-sm btn-outline-danger mb-2"/>
                    </div>
                    <div id="enseignement_content" className="enseignement">
                        <TextRender html={false} text={this.props.chapitre.content || ''} />
                    </div>
                </div>
                <div id="ficheCours__tableMatiere" className="ficheCours__tableMatiere paper">
                    <TableMatiere contents = {this.props.chapitre.contents || []} />
                </div>
            </div>  
        );
    }

}

Chapitre.default = {
    chapitre : {}
}

export default Chapitre;