import React from 'react';
import { Switch,Route} from 'react-router-dom'

import { BASE, ENSEIGNEMENT_HOME, ENSEIGNEMENT_LIST } from '../../routes/liens.js';
import Home from '../../pages/Home';
import { toLink } from '../../navigation/index.js';
import ListEnseignementLayout from '../ListEnseignementLayout';



class EnseignementLayout extends React.Component {
    
    constructor(props){
        super(props)
        
        // si la classe n'est defini => redirection
        const index = props.classes.findIndex(({code})=>(
            this.props.match.params.codeClasse===code
        ));
        if(index === -1){
            toLink(BASE);
        }

        this.state = {
            codeClasse : this.props.match.params.codeClasse
        }
    }

    componentDidMount(){        
        this.props.loadAppClasse(this.props.match.params.codeClasse);
    }

    static getDerivedStateFromProps(nextProps, prevState){
        const nextClasse = nextProps.match.params.codeClasse;
        const prevClasse = prevState.codeClasse;
        if(prevClasse && prevClasse !== nextClasse){
            nextProps.loadAppClasse(nextClasse)
            return {codeClasse : nextClasse};
        }
        return null;
    }
    
    render() {
        return (
            <React.Fragment>
                <Switch>                    
                    <Route exact path={ENSEIGNEMENT_HOME} component={Home} />
                    <Route exact path={ENSEIGNEMENT_LIST} component={ListEnseignementLayout} />
                    <Route exact path={`${ENSEIGNEMENT_LIST}/:enseignement`} component={ListEnseignementLayout} />
                </Switch>
            </React.Fragment>
        )
    }


}

// Exporte le composant
export default EnseignementLayout;