import React from 'react';

import {Row,Col,Alert,FormGroup,Input,FormFeedback,Label,Button} from 'reactstrap';
import { enregistrerChapitre, getChapitre } from '../../../../services/chapitreService';
import { toLink } from '../../../../navigation';
import { lienOuvrirChapitre } from '../../../../navigation/cours';

class EditerResume extends React.Component  {
    constructor(props){
        super(props);
        this.state = {
            id : null,
            title : '',
            resume : '',
            classe : props.classe.code,
            specialite : '',
            errors : {},
            error : false
        };
    }

    componentDidMount(){
        const {chapitreID} = this.props.match.params;
        if(chapitreID){
            getChapitre(chapitreID).then(({data})=>{
                this.setState({title : data.title,id : data.id,resume : data.resume, classe : data.programme.classe.code,specialite : data.programme.specialite.code});
                const classeID = this.props.classe.id;
                if(!classeID){
                    this.props.loadAppClasse(data.programme.classe.code);
                }
            })
        }else if(!this.state.classe){
            const classe = this.props.classes[0] ? this.props.classes[0].code : '';
            this.setState({classe});
            this.props.loadAppClasse(classe);
        }
    }
    
    handle = ({target})=>{
        this.setState(()=>{
            let newState = {};
            newState[target.name] = target.value;
            return newState;
        })
    }

    saveChapitre = ()=>{
        enregistrerChapitre({...this.state}).then(({data})=>{   
            toLink(lienOuvrirChapitre({chapitreID:data.id}));
        })
        .catch((errors)=>{
            console.log(errors)
            this.setState({...errors})
        })
    }
    
    
    render(){
        return (
            <div className="row">
                <div className="col-md-8 ml-auto mr-auto form-container">

                    <Alert color={this.state.error ? 'danger' : 'primary'} className="form-header" fade={false}>
                        <span className="form-title">
                            {this.state.error ? "Erreur survenu lors du traitement" : "Ajout d'un nouveau chapitre"}
                        </span>
                    </Alert>
                    <FormGroup>
                        <Label for="title">Titre du chapitre</Label>
                        <Input  
                            id="title"
                            name = 'title'
                            type="text" 
                            disabled={false}
                            value={this.state.title}
                            invalid={this.state.errors.title ? true : false}
                            onChange={this.handle}
                        />
                        <FormFeedback>{this.state.errors.title}</FormFeedback>
                    </FormGroup>
                    <Row form>
                        <Col md={6}>
                            <FormGroup>
                                <Label for="classe">Classe</Label>
                                <Input  
                                    id="classe"
                                    name = 'classe'
                                    type="select" 
                                    disabled={false}
                                    value={this.state.classe}
                                    invalid={this.state.errors.classe ? true : false}
                                    onChange={this.handle}
                                >
                                    {this.props.classes.map(({code,name})=>(<option key={code} value={code}>{name}</option>))}
                                </Input>
                                <FormFeedback>{this.state.errors.classe}</FormFeedback>
                            </FormGroup>
                        </Col>
                        <Col md={6}>
                            <FormGroup>
                                <Label for="specialite">Specialité</Label>
                                <Input  
                                    id="specialite"
                                    name = 'specialite'
                                    type="select" 
                                    disabled={false}
                                    value={this.state.specialite}
                                    invalid={this.state.errors.specialite ? true : false}
                                    onChange={this.handle}
                                >
                                    {!this.state.specialite && <option value="0">Selectionnez une specialité</option>}
                                    {this.props.classe.specialites.map(({code,name})=>(<option value={code} key={code}>{name}</option>))}
                                </Input>
                                <FormFeedback>{this.state.errors.specialite}</FormFeedback>
                            </FormGroup> 
                        </Col>
                    </Row>

                    <FormGroup>
                        <Label for="resume">Résumé du chapitre</Label>
                        <Input  
                            id="resume"
                            name = "resume"
                            type="textarea" 
                            disabled={false}
                            value={this.state.resume}
                            invalid={this.state.errors.resume ? true : false}
                            rows={5}
                            onChange={this.handle}
                        />
                        <FormFeedback>{this.state.errors.resume}</FormFeedback>
                    </FormGroup>

                    <div className="text-right">
                        <Button onClick={this.saveChapitre} color="primary" className="shadow-sm" >Enregistrer</Button>
                    </div>
                </div>
            </div>
        );
    }

}

export default EditerResume;