import {combineReducers} from 'redux';
import specialiteReducer from './specialiteReducer'
import appReducer from './appReducer';
import chapitreReducer from './chapitreReducer';
import userReducer from './userReducer';
import exerciceReducer from './exerciceReducer';
import controleReducer from './controleReducer';
export default combineReducers({
    specialiteReducer,
    exerciceReducer,
    chapitreReducer,
    controleReducer,
    appReducer,
    userReducer
});