import React from 'react';
import {Link} from 'react-router-dom';
import { lienOuvrirChapitre } from '../../../navigation/cours';


class Paginate extends React.Component {

    nextButton(){       
        const nextIndex = this.props.index + 1;
        if(nextIndex >= this.props.elements.length){
            return {};
        }
        return this.label(nextIndex);
    }

    label(index){
        return{
            url : lienOuvrirChapitre({chapitreID: this.props.elements[index].id, p : this.props.page}),
            title : this.props.elements[index].title
        }
    }

    prevButton(){
        const prevIndex = this.props.index - 1;
        if(prevIndex<0){
            return  {};   
        }
        return this.label(prevIndex);
    }
    
    render(){
        let paginate = null;
        if(this.props.left){
            paginate = this.prevButton();
            paginate.className = 'paginate-left';
        }else{
            paginate = this.nextButton();
            paginate.className = 'paginate-right';
        }
        console.log(paginate);
        return(
            <div className="paginate">
                {paginate.url &&<Link className={paginate.className} to={paginate.url} />}
            </div>
        )
    }
}

Paginate.default = {
    elements : [],
    index : -1
}
export default Paginate;