import {connect} from 'react-redux';

import VoirControle from './VoirControle.jsx';
import { getFullControle, isLoadingControle } from '../../../redux/reducers/controleReducer.js';
import { loadControle } from '../../../redux/actions/controlesActions.js';
import { getAppClasse } from '../../../redux/reducers/appReducer.js';

function mapStateToProps(state) {
    return {
        controle : getFullControle(state),
        loading : isLoadingControle(state),
        classe : getAppClasse(state)
    };
}

function mapDispatchToProps(dispatch) {
    return {
        loadControle: function(controleID){
            dispatch(loadControle(controleID));
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(VoirControle);
