import React from 'react';
import ListControles from './ListControles.js';
import { CODE_DEVOIRS } from '../../../routes/liens';

class ListDevoirs extends React.Component  {


    /**
     * Rendu de la page HTML
     */
    render(){
        return (
            <ListControles 
                codeClasse = {this.props.match.params.codeClasse}
                codeSpecialite = {this.props.match.params.codeSpecialite}
                type = {CODE_DEVOIRS} />
        );
    }

}

export default ListDevoirs;