import React from 'react';
import {Link} from 'react-router-dom';
import { getLienEnseignement, toLink } from '../../navigation';
import classnames from 'classnames';


class SideBar extends React.Component  {

    logout = ()=>{
        this.props.logout();
    }

    toggleAuth = ()=>{
        this.props.toggleAuth();
    }
    
    isConnect(){
        return this.props.user && this.props.user.token;
    }

    getRoleName(){
        const {role, classe} = this.props.user;
        if(role.code==="student"){
            return `Eleve (${classe.abreviation})`;
        }
        if(role.code==="teacher"){
            const size = this.props.user.specialites.length;
            if(size===0){
                return "Professeur";
            }
            let roleName = "Prof de ";
            this.props.user.specialites.forEach((specialite,i) => {
                roleName+= `${specialite.code} `;
            });
            return roleName;    
        }
    }

    renderProfil(){
        return(
            <React.Fragment>
                <div className="profil">
                    <img src={require('../../img/default.png')} alt=""/>
                    <h3 data-role={this.getRoleName()} className="name">
                        {this.props.user.firstname} {this.props.user.lastname}
                    </h3>
                </div>
                <div className="d-flex justify-content-end mt-3 mb-2">
                    <div  onClick={this.logout} className="navigation-item" style={{color:'#eee'}}>
                        deconnexion
                    </div>
                </div>
            </React.Fragment>
        )
    }

    btnLogout(){
        return(
            <div className="d-flex justify-content-between mt-3 mb-2">
                <div  onClick={this.toggleAuth} className="navigation-item">
                    S'inscrire
                </div>
                <div  onClick={this.toggleAuth} className="navigation-item">
                    Connexion
                </div>
            </div>
        )
    }

    openSpecialite(codeSpecialite){
        toLink(getLienEnseignement(this.props.classe.code, codeSpecialite));
    }

    render(){
        return (
            <div className="sidebar">
                {/*<div className="clearfix">
                    {this.isConnect() ? this.renderProfil() : this.btnLogout()}
        </div>*/}
                <nav className="navigation">
                    {this.props.classe.specialites.map(({name,code})=>(
                        <div
                            onClick={this.openSpecialite.bind(this,code)}
                            key={code} 
                            className={classnames("navigation-item",{active : this.props.specialite===code})}
                        >
                            <img src={require(`../../img/${code}-icon.png`)} alt=""/> {name}
                        </div>
                    ))}
                    <div className="navigation-item">
                        <img src={require(`../../img/book-icon.png`)} alt=""/>
                        Tous les livres
                    </div>
                </nav>
            </div>
        );
    }

}

export default SideBar;