import React from 'react';


import Editeur from '../../../commun/Editeur';
import { getChapitre, enregistrerContent } from '../../../services/chapitreService';

import {lienOuvrirChapitre} from '../../../navigation/cours';
import {toLink} from '../../../navigation';

class EditerContent extends React.Component  {
    constructor(props){
        super(props);
        this.state = {
            id : null,
            content : '',
            title : '',
            errors : {},
            error : false,
            loading : false
        };
    }

    componentDidMount(){
        const {chapitreID} = this.props.match.params;
        this.setState({loading : true});
        getChapitre(chapitreID).then(({data})=>{
            this.setState({ id : data.id,title : data.title,content : data.content,loading : false});
        }).catch((e)=>{
            this.setState({loading : false});
        })
    
    }
    
    handleContent = ({target})=>{
        this.setState({content : target.value});
    }

    updateContent = ()=>{
        this.setState({loading : true});
        enregistrerContent(this.state.id,this.state.content).then(({data})=>{
            toLink(lienOuvrirChapitre({chapitreID : data.id}));
        })
        .catch((e)=>{
            console.log(e);
            this.setState({loading : false});
        })
    }
    
    
    render() {

        //const {contenu,titre} = this.props.chapitre;
        
        return (
            <Editeur
                onSave = {this.updateContent}
                handleContent = {this.handleContent}
                isSaving = {false}
                isLoading={this.state.loading}
                isSaved = {false}
                title={this.state.title}
                content = {this.state.content} />
        );
    }

}

export default EditerContent;