import React from 'react';
import {Link} from 'react-router-dom';
import { lienGestionChapitre, lienOuvrirChapitre } from '../../../navigation/cours';
import ProtectedButton from '../../../commun/ProtectedButton';
import { toLink } from '../../../navigation';
import { CODE_EXAMENS } from '../../../routes/liens';
import { isClasseExamen, getControleTitle } from '../../../utils/fonctions';
import { lienOuvrirControle } from '../../../navigation/controles';

const iconExamen = <img src={require(`../../../img/examens-default.png`)} alt="" />

const iconDevoir = <img src={require(`../../../img/devoirs-default.png`)} alt="" />


class ListControles extends React.Component  {


    componentDidMount(){
        const {codeSpecialite,codeClasse} = this.props;
        this.props.loadControles(codeClasse,codeSpecialite,this.props.type);        
    }

    getSpecialite(){
        const specialite = this.props.classe.specialites.find((specialite)=>(
            specialite.code===this.props.codeSpecialite
        ));
        return specialite || {};
    }

    ajouterControles = ()=>{
        toLink(lienGestionChapitre());
    }


    /**
     * Rendu de la page HTML
     */
    render(){
        const specialite = this.getSpecialite();
        const controles = this.props.type===CODE_EXAMENS ? this.props.examens : this.props.devoirs;
        
        return (
            <div className="enseignement-list">
                <div className={"enseignement-title "+specialite.code}>
                    <h2 className="name">Devoirs de {specialite.name}</h2>
                </div>
                <div className="row">
                    <div className="col-12 clearfix">
                        <ProtectedButton onClick = {this.ajouterControles} title={"Ajouter "+this.props.type} className="btn btn-success mb-3 float-right"/>
                    </div>
                    {controles.map(({id,year},i)=>(
                        <div key={id} className="col-6">
                            <Item
                                type={this.props.type} 
                                id = {id} 
                                resume = {"resume"} 
                                title = {getControleTitle(this.props.classe.code,this.props.classe.type,this.props.type,year,i+1)} 
                            />
                        </div>                   
                    ))}
                </div>
            </div>  
        );
    }

}

const Item = ({title, resume, id,type})=>{

    return (
        <div className="enseignement-card" onClick={()=>toLink(lienOuvrirControle(id))}>
            <div className="enseignement-image">
                {type===CODE_EXAMENS ? iconExamen : iconDevoir}
            </div>
            
            <div className="card-body">              
                <h4 className="card-title">{title}</h4>
                <p className="card-text">Notions abordées </p>
                <div className="clearfix">
                    <p className="card-text float-left">
                        2 exercices
                    </p>
                    <p className="card-text float-right">
                        Maths 6°
                    </p>
                </div>
            </div>
        </div>
    )
}


export default ListControles;