import React from 'react';
import {Link} from 'react-router-dom';
import { CODE_COURS, CODE_EXAMENS, CODE_DEVOIRS } from '../../routes/liens';
import { toLink, getLienEnseignement } from '../../navigation';

const ENSEIGNEMENTS = [
    {
        title : "cours",
        code : CODE_COURS,
        resume : "Retrouver ici des cours et des exercices",
        image : require('../../img/cours-home.png')
    },
    {
        title : "devoirs",
        code : CODE_DEVOIRS,
        resume : "Exercez vous avec des sujets de devoirs",
        image : require('../../img/devoir-home.png')
    },    
    {
        title : "examens",
        code : CODE_EXAMENS,
        resume : "Retrouvez ici des sujets de BEPC et de BAC",
        image : require('../../img/examen-home.png')
    },    
    {
        title : "livres",
        code : "livre",
        resume : "Accéder à des livres electroniques dans cette section",
        image : require('../../img/book-home.png')
    }
]

const Enseignement = ({enseignement,codeClasse,codeSpecialite})=>{
    const {image,title,resume} = enseignement;
    return(
        <div className="enseignement-home shadow-sm">
            <div className="enseignement-image">
                <img src={image} alt="" />
            </div>
            <div>
                <h2 className="enseignement-title">{title}</h2>
                <div className="enseignement-resume">{resume}</div>
                <div className="text-right p-2">
                    <Link to={getLienEnseignement(codeClasse,codeSpecialite,enseignement.code)} className="btn btn-primary mt-3">Accéder</Link>
                </div>
            </div>
        </div>
    )
}


class Home extends React.Component  {

    constructor(props) {
        super(props);
        this.state = {
            openModal: false
        };
    }
    
    toggle = () => {
        this.setState({
            openModal: !this.state.openModal
        });
    }
    
    openClasse = (codeClasse,codeSpecialite)=>{
        //console.log(codeClasse,codeSpecialite)
        toLink(getLienEnseignement(codeClasse,codeSpecialite))
    }
    
    render(){
        return (
            <div className="home">
                <div className="container-fluid ">
                    <div className="row plateforme">
                        <div className="col-12">
                            <div className="jumbotron bg-transparent m-0">
                                <h1 className="display-2">Lonnya</h1>
                                <p className="lead">
                                    Plateforme de partage de connaisance
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="container">
                    <div className="enseignements mb-5" >
                        <div className="row">
                            {ENSEIGNEMENTS.map((enseignement)=>(
                                <div key={enseignement.code} className="col-md-3 col-12">
                                    <Enseignement  
                                        toggle={this.toggle}
                                        codeClasse = {this.props.classe.code}
                                        codeSpecialite = {this.props.specialite.code}
                                        enseignement={enseignement} 
                                    />
                                </div>
                            ))}
                        </div>
                    </div>
                </div>

                <div className="container-fluid ">
                    <div className="row bg-secondary">
                        <div className="col-12">
                            <div className="jumbotron text-center m-0 bg-transparent">
                                <p className="display-4 text-blue-light">
                                    FOOTER
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            
        );
    }

}

export default Home;