import { CODE_DEVOIRS, CODE_EXAMENS } from "../../routes/liens";
import { getControles, getControle } from "../../services/controleService";

export function loadControles(codeSpecialite='',codeClasse='',type=CODE_DEVOIRS){
    return function(dispatch){
        dispatch(setLoadingControles());
        getControles(codeSpecialite, codeClasse,type).then(({data})=>{
            if(type===CODE_EXAMENS){
                dispatch(setExamens(data));
            }else{
                dispatch(setDevoirs(data));
            }
            dispatch(setLoadingControles(false));
        })
        .catch((error)=>{
            dispatch(setLoadingControles(false));
            console.log(error);
        })
    }
}

