export function loadControle(controleID){
    return function(dispatch,getState){
        dispatch(setLoadingControle());
        getControle(controleID).then(({data})=>{
            
            console.log(data)
            dispatch(setControle(data));
            dispatch(setLoadingControle(false));
            /*const classeID = getAppClasse(getState()).id;
            if(classeID === undefined || classeID === null){
                dispatch(loadAppClasse(data.programme.classe.code));
            }*/
            //dispatch(setAppSpecialite(data.programme.specialite.code))
        })
        .catch((error)=>{
            dispatch(setLoadingControle(false));
            console.log(error);
        })
    }
}

//
function setControle(controle={}){
    return {type : "SET_CONTROLE", controle};
}
function setLoadingControle(loading=true){
    return {type : "LOADING_CONTROLE", loading};
}

//
function setLoadingControles(loading=true){
    return {type : "LOADING_CONTROLES", loading};
}
function setDevoirs(controles=[]){
    return {type : "SET_DEVOIRS",controles};
}
function setExamens(controles=[]){
    return {type : "SET_EXAMENS",controles};
}