import {connect} from 'react-redux';

//import {withRouter} from 'react-router-dom';

import ConnexionForm from './ConnexionForm.jsx';

//import {getAppClasses,getAppClasse} from '../../redux/reducers/appReducer';
//import { loadAppClasse } from '../../redux/actions/appActions';
import { login, toggleAuth } from '../../redux/actions/userActions';
import { isLogging, getToken,isOpenAuth,isAuthenError } from '../../redux/reducers/userReducer';

function mapStateToProps(state) {
    return {
        logging : isLogging(state),
        token : getToken(state),
        isAuthenError : isAuthenError(state),
        isOpenAuth : isOpenAuth(state)
    }
}

function mapDispatchToProps(dispatch) {
    return {
        login : function(identifiant,password){
            dispatch(login(identifiant,password));
        },
        toggleAuth : function(isOpen){
            dispatch(toggleAuth(isOpen));
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ConnexionForm);
