import request from './index';

const BASE = "/teachers"

export function saveEnseignant(enseignant) {
    let url = `${BASE}`;
    let method = "POST";
    const {id} = enseignant;
    
    if (id && id !== null && id !== "") {
        url = `${url}/${id}`;
        method = "PUT";
    }
    return request({
        url: url,
        data: {
            ...enseignant,
            id: null
        },
        method: method
    })
}

export function getEnseignantByUsername(username) {
    return request({
        url: `${BASE}/${username}`,
        method: "GET"
    })
}

export function getEnseignantSpecialites(enseignantID,classeID) {
    return request({
        url: `${BASE}/${enseignantID}/${classeID}/specialites`,
        method: "GET"
    })
}

export function getEnseignantClasses(enseignantID) {
    return request({
        url: `${BASE}/${enseignantID}/classes`,
        method: "GET"
    })
}

export function getEnseignants() {
    return request({
        url: `${BASE}`,
        method: "GET"
    })
}

export function getEnseignant(enseignantID) {
    return request({
        url: `${BASE}/${enseignantID}`,
    })
}
