import React from 'react';
import {Link} from 'react-router-dom';
import TimeLine from '../../../commun/TimeLine';
import { getLienEnseignement } from '../../../navigation';
import { CODE_DEVOIRS, CODE_EXAMENS } from '../../../routes/liens';
import Loading from '../../../commun/Loading';
import Presentation from '../../../commun/Presentation';
import ExerciceItem from '../../Exercices/ListExercice/ExerciceItem';
import { getControleTitle } from '../../../utils/fonctions';
import Paginate from '../../Cours/VoirCours/Pagination';



class VoirControle extends React.Component  {

    constructor(props){
        super(props);
        this.state = {
            //chapitreID : null
        }
    }

    componentDidMount(){
        window.scroll(0,0);
        this.props.loadControle(this.props.match.params.controleID);
        //this.setState({chapitreID : this.props.match.params.chapitreID});
    }

    editerControle = ()=>{

    }

    isDevoir(){
        return this.props.controle.type==='devoir';
    }

    makeTitle(typeControle){
        if(this.isDevoir()){
            return "Sujet de devoirs";
        }
        const {code,type} = this.props.classe;
        return getControleTitle(code,type,typeControle,this.props.controle.year);
    }

    render(){
        
        if(!this.props.controle.id){
            return <Loading />
        }
        
        const {programme} = this.props.controle;
        const name = this.isDevoir() ? "Devoirs" : "Examens";
        const code = this.isDevoir() ? CODE_DEVOIRS : CODE_EXAMENS;
        const title =  this.makeTitle(code);
        return (
            <div className="enseignement-container">
                <Paginate elements = {[]} index = {-1} page={0} left />
                <div>
                    <TimeLine 
                        name={name}
                        code={code}
                        title={title} 
                        programme={programme} 
                    />
                    
                    <div className="d-flex align-items-center justify-content-between mb-2">
                        <Link to={getLienEnseignement(programme.classe.code,programme.specialite.code,code)} 
                            className="btn btn-sm text-secondary icon-left m-0">
                            <span>Liste {name}</span>
                        </Link>
                        {this.props.loading && <Loading />}
                    </div>

                    <Presentation
                        className="controles"
                        title = {title}
                        resume = {"resume"}
                        editer = {this.editerControle}
                    />

                    <div className="shadow-sm rounded bg-white mt-2 p-3" >
                        <div className="exercices">
                            {this.props.controle.exercises.map((exercise)=>(
                                <ExerciceItem key={exercise.id} exercice = {exercise} />
                            ))}
                        </div>
                    </div>
                </div>
                <Paginate elements = {[]} index = {-1} page={0} />
            </div>
        );
    }

}


export default VoirControle;