import React from 'react'
import { Switch, Route } from 'react-router-dom'
import EditerResume from '../pages/Cours/EditerCours/EditerResume';
import VoirCours from '../pages/Cours/VoirCours';



const CoursLayout = (props) => {
    
    return(
        <div className="container">
            <Switch>
                <Route exact path={`${props.match.url}/lecture/:chapitreID`} component={VoirCours} />
                <Route excat path={`${props.match.url}/gestion/:chapitreID`} component={EditerResume} />
                <Route excat path={`${props.match.url}/gestion`} component={EditerResume} />
            </Switch>
        </div>
    );
}

export default CoursLayout