import request from './index';
import { CODE_DEVOIRS } from '../routes/liens';

const BASE_CONTROLE = "/controles";


export function getControles(codeSpecialite, codeClasse, type=CODE_DEVOIRS){
    return request({
        url: `${BASE_CONTROLE}/${type}/${codeSpecialite}/${codeClasse}`,
        method: "GET"
    });
}

export function getControle(controleID){
    return request({
        url: `${BASE_CONTROLE}/${controleID}`,
        method: "GET"
    });
}

export function getControleItem(id=-1){
    return request({
        url: `${BASE_CONTROLE}/items/${id}`,
        method: "GET"
    });
}

export function enregistrerControleItem(controleItem = {}) {
    let url = `${BASE_CONTROLE}/items`;
    let method = "POST";
    const {id} = controleItem;
    
    if (id && id !== null && id !== "") {
        url = `${url}/${id}`;
        method = "PUT";
    }
    
    return request({
        url: url,
        data: {
            ...controleItem,
            id: null
        },
        method: method
    })
}

export function supprimerControleItem(controleItemID) {
    return request({
        url: `${BASE_CONTROLE}/items/${controleItemID}`,
        method: "DELETE"
    })
}

export function enregistrerControle(type,controle = {}) {
    let url = `${BASE_CONTROLE}/${type}`;
    let method = "POST";
    const {id} = controle;
    
    if (id && id !== null && id !== "") {
        url = `${url}/${id}`;
        method = "PUT";
    }
    
    return request({
        url: url,
        data: {
            ...controle,
            id: null
        },
        method: method
    })
}

