import {connect} from 'react-redux';

import {withRouter} from 'react-router-dom';

import AppLayout from './AppLayout.jsx';
import { loadAppClasses } from '../../redux/actions/appActions';
import {getAppClasses, appClassesLoading, getAppClasse} from '../../redux/reducers/appReducer';
import { getUserConnected } from '../../redux/reducers/userReducer.js';


function mapStateToProps(state) {
    return {
        loading : appClassesLoading(state) /*|| loadingMe(state)*/,
        classes : getAppClasses(state),
        classe : getAppClasse(state),
        user : getUserConnected(state)
    };
}

function mapDispatchToProps(dispatch) {
    return {
        laodClasses : function(enseignantID){
            dispatch(loadAppClasses(enseignantID));
        }
    }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(AppLayout));
