import { deleteUser, setToken} from "../../utils/token";
//import { toHome } from "../../routes/navigation";
import {auth,me} from '../../services/userService';
import {toLink, getLienEnseignement, toHome } from "../../navigation";
import { isStudent, isTeacher } from "../../utils/fonctions";
import { loadAppClasses } from "./appActions";


/**
 * 
 */
export function login(email,password) {
    return function (dispatch) {
        dispatch(setLogging());
        auth(email, password).then((data) => {
            setToken(data.token);
            dispatch(userInfo(data.token));
            dispatch(toggleAuth());
            dispatch(setLogging(false));
        }).catch(({response}) => {
            dispatch(setLogging(false));
            dispatch(setAuthenError(true))
            if(!response){
                alert("Le serveur ne repond pas");
            }
        });
    }
}

export function logout(){
    return function(dispatch){
        deleteUser();
        dispatch(setUserConnected());
        dispatch(loadAppClasses());
        toHome();
    }
}

export function userInfo(token) {
    return function (dispatch) {
        dispatch(loadingMe(true));
        if(!token){
            dispatch(loadingMe(false));
            return;
        }
        me(token).then(({data}) => {
            dispatch(setUserConnected({...data, token : token}));
            dispatch(loadingMe(false));
            if(isStudent(data.role)){
                //toLink(getLienEnseignement(data.classe.code));
            }else if(isTeacher(data.role)){
                //toHome();
            }
        }).catch(({response}) => {
            //console.log(response);
            dispatch(loadingMe(false));
        })
    }
}

export function toggleAuth(){
    return {type: "TOGGLE_AUTH" };
}

export function setAuthenError(error=true){
    return {type: "AUTHEN_ERROR", error};
}
export function setLogged(logged){
    return {type: "LOGGED", logged : logged};
}
export function setLogging(logging=true){
    return {type: "LOGGING", logging };
}
export function setUserConnected(user={}){
    return {type: "SET_USER_CONNECTED", user };
}

function loadingMe(loading=true){
    return {type: "LOADING_ME", loading };
}