import {connect} from 'react-redux';

//import {withRouter} from 'react-router-dom';

import {getAppClasse, getAppSpecialite} from '../../redux/reducers/appReducer';
import {getUserConnected} from '../../redux/reducers/userReducer';
import {logout, toggleAuth} from '../../redux/actions/userActions';

import SideBar from './SideBar';

function mapStateToProps(state) {
    return {
        classe : getAppClasse(state),
        user : getUserConnected(state),
        specialite : getAppSpecialite(state)
    };
}

function mapDispatchToProps(dispatch) {
    return {
        logout : function(){
            dispatch(logout());
        },
        toggleAuth : function(isOpen){
            dispatch(toggleAuth(isOpen));
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(SideBar);
