import React from 'react';
import {Link} from 'react-router-dom';
import classnames from 'classnames';
import TextToLatex from '../../../commun/TextRender';
import { lienModifierEnonce, lienModifierCorrection } from '../../../navigation/cours';

export const ENONCE = 'e';
export const CORRECTION = 'c';

class ExerciceItem extends React.Component  {

    
    constructor(props){
        super(props);
        this.state = {
            page : ENONCE
        }
    }

    toggle(page){
        this.setState({page})
    }

    renderEnonce(){
        return (
            <React.Fragment>
                <TextToLatex text = {this.props.exercice.enonce} />
                <div className="text-right">
                    <Link to={lienModifierEnonce(this.props.exercice.id)}className="btn btn-outline-danger btn-sm">
                        Modifier l'enoncé
                    </Link>
                </div>
            </React.Fragment>
        )
    }

    renderCorrection(){
        return (
            <React.Fragment>
                <TextToLatex text = {this.props.exercice.correction} />
                <div className="text-right">
                    <Link to={lienModifierCorrection(this.props.exercice.id)} className="btn btn-outline-danger btn-sm">
                        Modifier la correction
                    </Link>
                </div>
            </React.Fragment>
        )
    }

    /**
     * Rendu de la page HTML
     */
    render(){
        return (
            <div className="exercice">
                <div className="tabbar text-left bg-transparent">
                    <div onClick={this.toggle.bind(this,ENONCE)}  className={classnames("tabbar-item",{active : this.state.page===ENONCE})}>
                        Exercice {this.props.num}
                    </div>
                    <div onClick={this.toggle.bind(this,CORRECTION)}  className={classnames("tabbar-item",{active : this.state.page===CORRECTION})}>
                        Correction 
                    </div>
                    <div className="tabbar-item text-danger">
                        <i className="fa fa-trash" /> 
                    </div>
                </div>

                <div className="content">
                    {this.state.page===ENONCE ? this.renderEnonce() : this.renderCorrection()}
                </div>
            </div>  
        );
    }

}


export default ExerciceItem;