
function initExercice(){
    return {
        id : null,
        titre : "",
        enonce : "",
        correction : "",
        chapitre : {}
    };
}
const initialState = {

    exercices : [],
    loadingExercices : false,

    chapitre : {},
    exercice : initExercice(),
    exerciceIsSaving: false,
    exerciceIsSaved: false,
    exerciceIsLoading: false,
    exerciceIsDeleting: false,
    errors : {}
}

export default function reducer(state = initialState, action = {}) {

    switch (action.type) {
        
        case "SET_CHAPITRE_EXERCICES" :
            return {...state, exercices : action.exercices};

        case "SET_LOADING_EXERCICES" :
            return {...state, loadingExercices : action.loadingExercices};
            
        default:
            return state;
    }
}

export const getChapitreExercices = ({exerciceReducer})=>(exerciceReducer.exercices);
export const loadingExercices = ({exerciceReducer})=>(exerciceReducer.loadingExercices);