import React from 'react';
import { Switch,Redirect,Route } from 'react-router-dom'

//import ConnectedRoute from '../../AuthorizedRoute.js';

import SideBar from '../../ui/SideBar';
import AppHeader from '../../ui/Header';
import { ENSEIGNEMENT_BASE, CODE_COURS } from '../../routes/liens.js';
import EnseignementLayout from '../EnseignementLayout';

import { isTeacher } from '../../utils/fonctions';
import CoursLayout from '../CoursLayout.js';
import ControleLayout from '../ControleLayout';
import Home from '../../pages/Home';

class AppLayout extends React.Component {

    componentDidMount(){
        
        if(this.props.classes.length !== 0){
            return;
        }
        if(isTeacher(this.props.user.role)){
            //this.props.laodClasses(this.props.user.id);
        }else{
            //this.props.laodClasses();
        }
    
    }


    redirect(){
        let content = null;
        if(this.props.match.isExact){
            let code = this.props.classes[0] && this.props.classes[0].code;
            content = code && (<Redirect  to={`${ENSEIGNEMENT_BASE}/${code}`} />);
        }
        return content;
    }
    
    render() {
        if(this.props.loading){
            //return null;
        }
        return (
            <React.Fragment>
                
                <AppHeader />
                
                {/*<SideBar />*/}
                <Switch>
                    <Route  path={`/${CODE_COURS}`} component={CoursLayout} />
                    <Route  path={`/controles`} component={ControleLayout} />
                    <Route exact path={`${this.props.match.path}e/:codeClasse`} component={EnseignementLayout} />
                    <Route exact path={`${this.props.match.path}e`} component={EnseignementLayout} />
                    <Route exact path={`${this.props.match.path}e/:codeClasse/:codeSpecialite`} component={EnseignementLayout} />
                    <Route exact path={`${this.props.match.path}e/:codeClasse/:codeSpecialite/:enseignement`} component={EnseignementLayout} />
                    {/*this.redirect()*/}
                        <Route exact path="/" component={Home} />
                </Switch>

            </React.Fragment>
        )
    }


}

export default AppLayout;