import request from './index';

const BASE = "/exercises"

export function getExerciceById(exerciceID){
    return request({
        url: `${BASE}/${exerciceID}`,
        method: "GET"
    })
}

export function getExercicesByIdChapitre(idChapitre) {
    return request({
        url: `${BASE}/chapitre/${idChapitre}`,
        method: "GET"
    })
}

export function supprimerExercice(idExercice){
    return request({
        url: `${BASE}/${idExercice}`,
        method: "DELETE"
    })
}

export function saveExercice({id,chapitreID,enonce,correction,online}){
    let formData = new FormData();
    if(id){
        formData.append('id', id);    
    }
    formData.append('enonce', enonce);
    formData.append('correction', correction);
    formData.append('online', online);
    formData.append('chapter', chapitreID);       
    return request({
        url: BASE,
        data: formData,
        method: "POST"
    })
}