import {connect} from 'react-redux';

//import {withRouter} from 'react-router-dom';

import {getAppClasse, appClasseLoading} from '../../redux/reducers/appReducer';

import ListEnseignementLayout from './ListEnseignementLayout.jsx';
import { setAppSpecialite } from '../../redux/actions/appActions';


function mapStateToProps(state) {
    return {
        classe : getAppClasse(state),
        isClasseLoading : appClasseLoading(state)
    };
}

function mapDispatchToProps(dispatch) {
    return {
        setSpecialite : function(codeSpecialite){
            dispatch(setAppSpecialite(codeSpecialite));
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ListEnseignementLayout);
