import React from 'react';
import {Link} from 'react-router-dom';
import { Row, Col, FormGroup, Label, Alert, Input,Button } from 'reactstrap';

class ConnexionForm extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            email : 'zakaridiadiawara@gmail.com',
            password : 'secret'
        };
    }

    handle = ({target})=>{
        this.setState(()=>{
            let newState = {};
            newState[target.name] = target.value;
            return newState;
        })
    }

    onLogin = ()=>{
        this.props.login(this.state.email,this.state.password)
    }

    render(){
        return(
            <div>
                <Alert color={this.props.isAuthenError ? 'danger' : 'primary'} fade={false}>
                    {this.props.isAuthenError ? "Erreur d'authentification" : 'Renseigner vos identifiants'}
                </Alert>
                <Row form >
                    <Col sm={12} >
                        <FormGroup>
                            <Label for="exampleEmail">Adresse email</Label>
                            <Input
                                disabled={this.props.logging} 
                                value={this.state.email}
                                onChange={this.handle}
                                name="email"
                            />
                        </FormGroup>
                    </Col>
                    <Col sm={12}>
                        <FormGroup>
                            <Label for="examplePassword">Password</Label>
                            <Input 
                                disabled={this.props.logging} 
                                value={this.state.password}
                                onChange={this.handle}
                                type="password" 
                                name="password"
                            />
                        </FormGroup>
                    </Col>
                    <Col className="col-12 text-right">
                        <Link to="/" className="mr-3 text-secondary" onClick={()=>{}}>Mot de passe oublié ? </Link>{' '}
                        <Button disabled={this.props.logging} color="primary" className="shadow-sm" onClick={this.onLogin}>Se connecter</Button>
                    </Col>
                </Row>  
            </div>
        )
    }
}

export default ConnexionForm;