import request from './index';

const BASE = "/classes"


export function getClasses() {
    return request({
        url: BASE,
        method: "GET"
    })
}

export function getClasse(classeID, withSpecialite=false) {
    const url = `${BASE}/${classeID}`;
    return request({
        url: withSpecialite ? `${url}?s=1` : url,
        method: "GET"
    })
}


export function saveClasse(classe) {
    let url = `${BASE}`;
    let method = "POST";
    const {id} = classe;
    
    if (id && id !== null && id !== "") {
        url = `${url}/${id}`;
        method = "PUT";
    }
    return request({
        url: url,
        data: {
            ...classe,
            id: null
        },
        method: method
    })
}

