import {getClasses, getClasse} from '../../services/classeService';
import { getAppClasses, getAppClasse } from '../reducers/appReducer';
import { getEnseignantClasses } from '../../services/enseignantService';
import { getUserConnected } from '../reducers/userReducer';
import { isTeacher } from '../../utils/fonctions';

/**
 * 
 */
export function loadAppClasses(enseignantID){
    return function(dispatch){
        dispatch(appClassesLoading());
        if(enseignantID){
            getEnseignantClasses(enseignantID).then((data)=>{
                console.log(data)
                dispatch(setAppClasses(data));
                dispatch(loadAppClasse(data[0] ? data[0].code : null));
                dispatch(appClassesLoading(false));
            }).catch((e)=>{
                console.log(e)
            })
        }
        else{
            getClasses().then((classes)=>{
                dispatch(setAppClasses(classes));
                dispatch(loadAppClasse());
                dispatch(appClassesLoading(false));
            })
            .catch((e)=>{
                console.log(e.response)
            })
        }

    }
}




export function loadAppClasse(code){
    return function(dispatch,getState){
        const classes = getAppClasses(getState());
        const codeClasse = code || classes[0] ? classes[0].code : '';

        const index = classes.findIndex(({code})=>(codeClasse===code));
        
        const classe = getAppClasse(getState());
        // la classe n'est pas defini
        if(index === -1 || (classe && classe.code === codeClasse)){
            return;
        }
        let newClasses = null;
        if(isTeacher(getUserConnected(getState()).role)){
            newClasses = {...classes[index], specialites : getUserConnected(getState()).specialites};
        }else{
            newClasses = {...classes[index]};
        }
        dispatch(setAppClasse(newClasses));
        dispatch(setAppSpecialite(newClasses.specialites[0] || {}));
    }
}

///
function appClassesLoading(loading=true){
    return {type:"APP_CLASSES_LOADING",loading}
}

function appClasseLoading(loading=true){
    return {type:"APP_CLASSE_LOADING",loading}
}

export function setAppClasses(classes=[]){
    return {type:"SET_APP_CLASSES",classes}
}

export function setAppClasse(classe=''){
    return {type:"SET_APP_CLASSE",classe}
}

export function setAppSpecialite(specialite=''){
    return {type:"SET_APP_SPECIALITE",specialite}
}


