const initialState = {
    
    devoirs : [],
    examens : [],
    loadingControles : false,

    controle : {},
    loadingControle : false,

}

export default function reducer(state = initialState, action = {}) {

    switch (action.type) {

        
        // List devoirs
        case "SET_DEVOIRS":
            return {...state, devoirs : action.controles };
        // List examens
        case "SET_EXAMENS":
            return {...state, examens : action.controles };
        case "LOADING_CONTROLES":
            return {...state, loadingControles : action.loading };

        // Controle
        case "SET_CONTROLE":
            return {...state, controle : action.controle};
        case "LOADING_CONTROLE":
            return {...state, loadingControle : action.loading };

        default:
            return state;
    }
}

export const getDevoirs = ({controleReducer})=>(controleReducer.devoirs);
export const getExamens = ({controleReducer})=>(controleReducer.examens);
export const isLoadingControles = ({controleReducer})=>(controleReducer.loadingControles)

export const getFullControle = ({controleReducer})=>(controleReducer.controle);
export const isLoadingControle = ({controleReducer})=>(controleReducer.loadingControle);
