import {connect} from 'react-redux';

//import {withRouter} from 'react-router-dom';

import Header from './Header';

import {getAppClasses,getAppClasse, getAppSpecialite} from '../../redux/reducers/appReducer';
import { loadAppClasse } from '../../redux/actions/appActions';

import {getUserConnected} from '../../redux/reducers/userReducer';
import {logout, toggleAuth} from '../../redux/actions/userActions';

function mapStateToProps(state) {
    return {
        classes : getAppClasses(state),
        user : getUserConnected(state),
        classe : getAppClasse(state),
        specialite : getAppSpecialite(state)
    }
}

function mapDispatchToProps(dispatch) {
    return {
        loadAppClasse : function(classeID){
            dispatch(loadAppClasse(classeID));
        },
        logout : function(){
            dispatch(logout());
        },
        toggleAuth : function(){
            dispatch(toggleAuth());
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Header);
