import React from 'react';

import ProtectedButton from './ProtectedButton';

class Presentation extends React.Component{

    render(){
        const {title,resume} = this.props;
        //shadow-sm rounded 
        return(
            <div className={"presentation shadow-sm "+this.props.className}>
                <div className="clearfix">
                    <h2 className="titre">
                        {title}
                        <ProtectedButton
                            onClick = {()=>{}} 
                            title={<i className="fa fa-trash"></i>}
                            className="ml-2 btn btn-outline-danger btn-sm"/>
                    </h2>
                </div>
                <p className="resume mb-0">
                    {resume}
                </p>
                <div className="text-right">
                    <ProtectedButton 
                        onClick={this.props.editer}
                        title="Modifier résumé"
                        className="btn btn-outline-danger btn-sm"/>
                </div>
                {this.props.children}
            </div>
        )
    }
}

Presentation.default = {
    title : '',
    resume : '',
    className : '',
    page : ''
}

export default Presentation;