import React from 'react';
import {Link} from 'react-router-dom';
import classnames from 'classnames';
import qs from 'qs';

import Loading from '../../../commun/Loading';

import { getLienEnseignement, toLink } from '../../../navigation';

import { CODE_COURS } from '../../../routes/liens';

import Chapitre from './Chapitre';
import Presentation from '../../../commun/Presentation';
import TimeLine from '../../../commun/TimeLine';
import Paginate from './Pagination';

import ListExercice from '../../Exercices/ListExercice';
import { lienOuvrirChapitre, lienGestionChapitre } from '../../../navigation/cours';

export const TAB_QUIZ = "q";
export const TAB_FICHE = "f";
export const TAB_EXERCICES = "e"

export const  COURS_TABS = [
    {
        name : "Fiche",
        p : TAB_FICHE
    },
    {
        name : "Quiz",
        p : TAB_QUIZ
    },
    {
        name : "Exercices",
        p : TAB_EXERCICES
    }
]


/**
 * Renvoie la page courante en fonction des params : La page des cours ou des exercices
 * @param {*} search 
 */
function getPage({search}){
    const {p} = qs.parse(search,{ ignoreQueryPrefix: true });
    for (let i = 0; i < COURS_TABS.length; i++) {
        if(COURS_TABS[i].p===p){
            return p
        }
    }
    return COURS_TABS[0].p;
}

class VoirCours extends React.Component  {

    constructor(props){
        super(props);
        this.state = {
            page : getPage(this.props.location),
            chapitreID : null
        }
    }

    componentDidMount(){
        window.scroll(0,0);
        this.props.loadCours(this.props.match.params.chapitreID);
        this.setState({chapitreID : this.props.match.params.chapitreID});
    }


    static getDerivedStateFromProps(nextProps, prevState){
        const {chapitreID} = nextProps.match.params;
        if(prevState.chapitreID && chapitreID.toString() !== prevState.chapitreID){
            nextProps.loadCours(chapitreID);
            window.scroll(0,0);
            return {chapitreID};
        }
        const p = getPage(nextProps.location);
        if(p!==prevState.page){
            return {page:p};
        }
        return null;
    }

    editerChapitre = ()=>{
        toLink(lienGestionChapitre(this.props.chapitre.id));
    }
    

    renderCoursTab(){
        const {page} = this.state;
        const {chapitreID} = this.props.match.params;
        switch (page) {
            case TAB_QUIZ:
                return(<div>Quiz</div>);
            case TAB_EXERCICES:
                return <ListExercice chapitreID = {chapitreID} />       
            default: 
                return <Chapitre content={this.props.chapitre || {}} />
        }
    }

    render(){

        if(!this.props.chapitre.id){
            return(
                <div className="container">
                    <Loading />
                </div>
            )
        }

        let index  = this.props.chapitres.findIndex((item)=>(
            this.props.chapitre.id.toString()===item.id.toString()
        ));
        
        const {programme} = this.props.chapitre;
        return (
            
            <div className="enseignement-container">
                <Paginate elements = {this.props.chapitres} index = {index} page={this.state.page} left />
                <div>
                    <TimeLine
                        code={CODE_COURS}
                        name="Cours"
                        title={this.props.chapitre.title} programme={programme} />
                    
                    <div className="d-flex align-items-center justify-content-between mb-2">
                        <Link to={getLienEnseignement(programme.classe.code,programme.specialite.code,CODE_COURS)} 
                            className="btn btn-sm btn-outline-secondary icon-left m-0">
                            <span>Liste des cours</span>
                        </Link>
                        {this.props.isLoadingChapitre && <Loading />}
                    </div>
                    
                    <Presentation
                        title = {this.props.chapitre.title}
                        resume = {this.props.chapitre.resume}
                        editer = {this.editerChapitre}
                    >
                        <div className="tabbar text-left" style={{backgroundColor:'transparent'}}>
                            {COURS_TABS.map((tab)=>(
                                <Link className={classnames("tabbar-item",{active:this.state.page===tab.p})} key={`tab_${tab.p}`} to={lienOuvrirChapitre({chapitreID :this.props.chapitre.id, p : tab.p})}>
                                    {tab.name}
                                </Link>
                            ))}
                        </div>
                    </Presentation>

                    <div className="enseignement-content shadow-sm" >
                        {this.renderCoursTab()}    
                    </div>
                </div>
                <Paginate  elements = {this.props.chapitres} index = {index} page={this.state.page}/>

            </div>
        );
    }

}


export default VoirCours;