import React from 'react';
import ConnexionForm from './ConnexionForm.js';
import { Modal, ModalBody} from 'reactstrap';

import { TabContent, TabPane} from 'reactstrap';
import classnames from 'classnames';

//import { getToken } from '../../utils/token';

class Connexion extends React.Component  {

    constructor(props) {
        super(props);
    
        this.toggle = this.toggle.bind(this);
        this.state = {
            activeTab: '1'
        };
    }
    
    toggle(tab) {
        if (this.state.activeTab !== tab) {
            this.setState({activeTab: tab});
        }
    }
            
    onLogin = (email, password)=>{
        this.props.login(email,password)
    }

    render(){
        return (

            <Modal className="auth-container" fade={false} isOpen={this.props.isOpenAuth} toggle={this.props.toggleAuth}>
                    <div className="tabbar text-left" style={{backgroundColor:'transparent'}}>
                        <div onClick={() => { this.toggle('1'); }} className={classnames("tabbar-item",{active:this.state.activeTab === '1'})} >
                            Connexion
                        </div>
                        <div onClick={() => { this.toggle('2'); }} className={classnames("tabbar-item",{active:this.state.activeTab === '2'})} >
                            Inscription
                        </div>
                    </div>
                    <ModalBody>
                        <TabContent activeTab={this.state.activeTab}>
                            <TabPane tabId="1">
                                <ConnexionForm />
                            </TabPane>
                            <TabPane tabId="2">
                                Inscription
                            </TabPane>
                        </TabContent>
                    </ModalBody>
            </Modal>
        );
    }

}


export default Connexion;