import request from './index';

const BASE_CHAPITRE = "/chapters"

export function getChapitres(codeClasse,codeSpecialite) {
    return request({url: `${BASE_CHAPITRE}/${codeClasse}/${codeSpecialite}`, method: "GET"})
}


export function getChapitre(chapitreID,full=true) {
    const url = `${BASE_CHAPITRE}/${chapitreID}?full=${full?'1':'0'}`;
    return request({url: url, method: "GET"})
}


export function enregistrerContent(id,content){
    let formData = new FormData();
    formData.append('content', content);
    return request({
        url: `${BASE_CHAPITRE}/${id}/update-content`,
        data: formData,
        method: "POST"
    })
}

export function enregistrerChapitre(chapitre = {}) {
    let formData = new FormData();
    if(chapitre.id){
        formData.append('id', chapitre.id);
    }
    formData.append('title', chapitre.title);
    formData.append('resume', chapitre.resume);
    formData.append('classe', chapitre.classe);
    formData.append('specialite', chapitre.specialite);

    return request({
        url: BASE_CHAPITRE,
        data: formData,
        method: "POST"
    })
}


export function getExercices(chapitreID){
    return request({
        url: `${BASE_CHAPITRE}/${chapitreID}/exercises`,
        method: "GET"
    })
}