import {connect} from 'react-redux';

import EditerResume from './EditerResume';
import { isOpenChapitreModal } from '../../../../redux/reducers/chapitreReducer';
import { toggleChapitreModal } from '../../../../redux/actions/chapitresActions';
import { getAppClasses, getAppClasse } from '../../../../redux/reducers/appReducer';
import { loadAppClasse } from '../../../../redux/actions/appActions';

function mapStateToProps(state) {
    return {
        isOpen : isOpenChapitreModal(state),
        classes : getAppClasses(state),
        classe : getAppClasse(state)
    };
}

function mapDispatchToProps(dispatch) {
    return {
        loadAppClasse : function(codeClasse){
            dispatch(loadAppClasse(codeClasse));
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(EditerResume);
