import React from 'react';
import qs from 'qs';
import Editeur from '../../../commun/Editeur';
import { getChapitre } from '../../../services/chapitreService';
import { getExerciceById, saveExercice  } from '../../../services/exerciceService';
import { ENONCE } from '../ListExercice/ExerciceItem';
import { lienOuvrirChapitre } from '../../../navigation/cours';
import { toLink } from '../../../navigation';


class EditerExercice extends React.Component  {
    
    constructor(props){
        super(props);
        const {p} = qs.parse(this.props.location.search,{ ignoreQueryPrefix: true });
        this.state = {
            id : null,
            content : '',
            enonce : '',
            correction : '',
            online : '1',
            title : '',
            chapitreID : '',
            page : p ? p : ENONCE,
            errors : {},
            error : false,
            loading : false
        };
    }

    isEnonce(){
        return this.state.page===ENONCE;
    }

    componentDidMount(){
        const {exerciceID,chapitreID} = this.props.match.params;
        if(chapitreID){
            this.setState({loading:true});
            getChapitre(chapitreID,false).then(({data})=>{
                this.setState({
                    title : "Exercice : "+data.title,
                    chapitreID : data.id,
                    loading : false
                });
            });
        }else if(exerciceID){
            this.setState({loading:true});
            getExerciceById(exerciceID).then(({data})=>{
                let content = this.isEnonce() ? data.enonce : data.correction;
                this.setState({
                    id:data.id,
                    title : "Exercice : "+data.chapter.title,
                    chapitreID : data.chapter.id,
                    content : content ? content : '',
                    enonce : data.enonce,
                    correction : data.correction,
                    online : data.online,
                    loading : false
                });
            });
        }
    }

    handleContent = ({target})=>{
        this.setState({content : target.value});
    }

    save = ()=>{
        const exercice = {...this.state};
        if(this.isEnonce()){
            exercice.enonce = exercice.content;
        }else{
            exercice.correction = exercice.content;
        }
        this.setState({loading:true});
        saveExercice(exercice).then(({data})=>{
            this.setState({loading:false});
            toLink(lienOuvrirChapitre({chapitreID:this.state.chapitreID,p:'e'}));
        })
        .catch((e)=>{
            console.log(e)
        })
    }

    /**
     * Rendu de la page HTML
     */
    render(){
        return (
            <Editeur
                onSave = {this.save}
                handleContent = {this.handleContent}
                isSaving = {false}
                isLoading={this.state.loading}
                isSaved = {false}
                title={this.state.title}
                content = {this.state.content} 
            />
        );
    }

}


export default EditerExercice;