import React from 'react';
import {Link} from 'react-router-dom';
import { Dropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';
import {  getLienEnseignement, toLink } from '../../navigation';
import { isClasseExamen } from '../../utils/fonctions';
import { CODE_COURS, CODE_DEVOIRS, CODE_EXAMENS, HOME } from '../../routes/liens';

const SpecialiteItem = ({classeAbreviation,specialite})=>(
    <div className="specialite-item icon" key={classeAbreviation+specialite.code}>
        <img src={require(`../../img/${specialite.code}-img.png`)}  alt="logo"/>
        <span data-classe={classeAbreviation} className="specialite-title">{specialite.name}</span>
    </div>
)

const ClasseItem = ({classe})=>(
    <div key={classe.id} className="menu-dropdown-item" >
        <span className="arrow-right">{classe.name} - {classe.abreviation}</span>
        <div className="menu-dropdown-sub-container">
            <div className="menu-dropdown-sub shadow-sm">
                {classe.specialites && classe.specialites.map((specialite)=>(
                    <SpecialiteItem  
                        key={classe.code+specialite.code} 
                        classeAbreviation={classe.abreviation} 
                        specialite={specialite} />
                ))}
            </div>
        </div>
    </div>
)

const MenuDropdown = ({classes})=>{
    //console.log(classes)
    return(
        <div className="menu-dropdown">
            <div className="menu-image">
                <img src={require('../../img/menu.png')}  alt="logo"/>
                <span>Sixieme 6°</span>
            </div>
            <div className="menu-dropdown-content shadow-sm">
                {classes.map((classe)=>(
                    <ClasseItem key={classe.code}  classe={classe} />
                ))}
            </div>
        </div>
    )
}

class MenuSpecialite extends React.Component {

    constructor(props) {
        super(props);
        this.toggle = this.toggle.bind(this);
        this.state = {
            dropdownOpen: false
        };
    }
    
    toggle() {
        this.setState(prevState => ({
            dropdownOpen: !prevState.dropdownOpen
        }));
    }

    openSpecialite(codeSpecialite){
        toLink(getLienEnseignement(this.props.codeClasse,codeSpecialite));
    }

    render(){
        const {specialite,specialites} = this.props;
        if(!specialite){
            return null;
        }
        return (
            <Dropdown isOpen={this.state.dropdownOpen} toggle={this.toggle}>
                <DropdownToggle color="default" className="bg-transparent text-secondary" caret>
                    {specialite.name}
                </DropdownToggle>
                <DropdownMenu>
                    {specialites && specialites.length-1 !==0 && 
                        <div className="menu-dropdown-content">
                            {specialites.map((item)=>(
                                item.id !== specialite.id &&
                                    <DropdownItem onClick={this.openSpecialite.bind(this,item.code)}  key={item.code}>
                                        <div key={item.code} className="specialite-item">    
                                            <img src={require(`../../img/${item.code}-img.png`)}  alt="logo"/>
                                            <span className="specialite-title">{item.name}</span>
                                        </div>
                                    </DropdownItem>
                            ))}
                        </div>
                    }
                </DropdownMenu>
            </Dropdown>
        );
    /*return(
        <div className="menu-specialite">
            <div className="menu-dropdown">
                <div className="specialite-item">    
                    <img src={require('../../img/maths-img.png')}  alt="logo"/>
                    <span data-classe={classeAbreviation} className="specialite-title">{specialite.name}</span>
                </div>
                {specialites && specialites.length-1 !==0 && 
                    <div className="menu-dropdown-content">
                        {specialites.map((item)=>(
                            item.id !== specialite.id &&
                            <div key={item.code} className="specialite-item">    
                                <img src={require(`../../img/${item.code}-img.png`)}  alt="logo"/>
                                <span data-classe={classeAbreviation} className="specialite-title">{item.name}</span>
                            </div>
                        ))}
                    </div>
                }
            </div>
        </div>
    )
   */
    }
}

const InputSearch = ()=>(
    <div className="input-group search w-50">
        <input name="search" placeholder="Rechercher un cours ..."  className="form-control" />
        <div className="input-group-append">
            <button className="btn btn-secondary search-btn" type="button">
                <i className="fa fa-search search-icon"></i>
            </button>
        </div>
    </div>
)

class Authentification extends React.Component{
    
    isConnect(){
        return this.props.user && this.props.user.token;
    }

    toggleAuth = ()=>{
        this.props.toggleAuth();
    }

    btnLogin(){
        return(
            <div className="header-auth">
                <div onClick={this.toggleAuth} className="btn btn-outline-secondary shadow-sm">Se connecter</div>
                <div className="btn btn-rouge ml-2 shadow-sm">S'inscrire</div>
            </div>
        )
    }

    renderProfil(){
        return(
            <React.Fragment>
                <div className="profil" onClick={this.props.logout} data-name={this.props.user.firstname[0] + this.props.user.lastname[0]}>
                </div>

            </React.Fragment>
        )
    }

    render(){
        return(
            <React.Fragment>
                <div className="clearfix">
                    {this.isConnect() ? this.renderProfil() : this.btnLogin()}
                </div>
                <div className="header-icon">
                    <img src={require('../../img/menu.png')}  alt="logo"/>
                </div>
            </React.Fragment>
        )
    }
}

const MenuItems = ({classe,codeSpecialite})=>(
    <div className="menu-items">
        <Link to={getLienEnseignement(classe.code,codeSpecialite,CODE_COURS)} className="menu-item">
            cours
        </Link>
        <Link to={getLienEnseignement(classe.code,codeSpecialite,CODE_DEVOIRS)} className="menu-item">
            devoirs
        </Link>
        {isClasseExamen(classe.type || {}) &&
            <Link to={getLienEnseignement(classe.code,codeSpecialite,CODE_EXAMENS)} className="menu-item">
                examens
            </Link>
        }
        <Link to={getLienEnseignement(classe.code,codeSpecialite,"livres")} className="menu-item">
            livres
        </Link>
    </div>
)

const AppHeader = ({classe,classes,specialite,toggleAuth,user,logout})=>{
    return(      
        <header className="container-fluid header shadow-sm">
            <div className="container">
                <div className="header-container">
                    <Link to={HOME}>
                        <div className="header-logo">
                            <img src={require('../../img/logo.png')} alt="Lonnya" />
                        </div>
                    </Link>
                    <InputSearch />
                    <Authentification
                        user = {user}
                        logout = {logout}
                        toggleAuth = {toggleAuth}
                    />
                </div>
                <div className="menu">
                    <MenuDropdown 
                        classes={classes} 
                    />
                    <MenuSpecialite 
                        specialite = {specialite}
                        codeClasse = {classe.code}
                        specialites = {classe.specialites}
                    />
                    <MenuItems
                        classe = {classe}
                        codeSpecialite = {specialite.code || null}
                    />
                </div>
            </div>
        </header>
    );
}




export default AppHeader;