import {connect} from 'react-redux';

import ListCours from './ListCours.jsx';
import { getAppClasse } from '../../../redux/reducers/appReducer.js';
import { loadChapitres } from '../../../redux/actions/chapitresActions';

import {getListChapitres,isLoadingChapitres} from '../../../redux/reducers/chapitreReducer';

function mapStateToProps(state) {
    return {
        classe : getAppClasse(state),
        chapitres : getListChapitres(state),
        isLoadingChapitres : isLoadingChapitres(state)
    };
}

function mapDispatchToProps(dispatch) {
    return {
        loadChapitres: function(codeSpecialite,codeClasse){
            dispatch(loadChapitres(codeSpecialite,codeClasse));
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ListCours);
