import React from 'react';

import {connect} from 'react-redux';

import { withRouter, Switch, Redirect, Route} from 'react-router-dom'

import App from '../layouts/AppLayout';
import { BASE } from '../routes/liens.js';
import { userInfo } from '../redux/actions/userActions.js';
import { getToken } from '../utils/token.js';
import Authentification from '../pages/Authentification'
import { loadingMe } from '../redux/reducers/userReducer';
import EditerContent from './Cours/EditerCours/EditerContent';
import EditerExercice from './Exercices/EditerExercice';


class Pages extends React.Component {

    constructor(props){
        super(props);
        props.getUserInfo();
    }

    render() {
        if(this.props.loading){
            return null;
        }
        
        return (
            <React.Fragment>
                <Switch>
                    <Route exact path={"/exercices/:chapitreID/creer"} component={EditerExercice} />
                    <Route exact path={"/exercices/:exerciceID/modifier"} component={EditerExercice} />
                    <Route exact path={"/cours/gestion/:chapitreID/modifier"} component={EditerContent} />
                    <Route path={BASE} component={App} />
                    <Redirect to={BASE} />
                </Switch>
                <Authentification />
            </React.Fragment>
        )
    }
}

function mapStateToProps(state) {
    return {
        loading : loadingMe(state)
    };
}

function mapDispatchToProps(dispatch) {
    return {
        getUserInfo : function(){
            dispatch(userInfo(getToken()));
        }
    }
}

export default withRouter(connect(mapStateToProps,mapDispatchToProps)(Pages));
