import React from 'react';
import {Link} from 'react-router-dom';
import { lienOuvrirChapitre, lienGestionChapitre } from '../../../navigation/cours';
import { TAB_QUIZ, TAB_EXERCICES, TAB_FICHE } from '../VoirCours/VoirCours.jsx';
import ProtectedButton from '../../../commun/ProtectedButton';
import { toLink } from '../../../navigation';
import Loading from '../../../commun/Loading';

class ListCours extends React.Component  {


    componentDidMount(){
        const {codeSpecialite,codeClasse} = this.props.match.params;
        this.props.loadChapitres(codeClasse,codeSpecialite);
    }

    getSpecialite(){
        const specialite = this.props.classe.specialites.find((specialite)=>(
            specialite.code===this.props.match.params.codeSpecialite
        ));
        return specialite || {};
    }

    ajouterCours = ()=>{
        toLink(lienGestionChapitre());
    }

    /**
     * Rendu de la page HTML
     */
    render(){
        const specialite = this.getSpecialite();
        return (
            <div className="enseignement-list col-12">
                <div className="enseignement-title clearfix">
                    <h2 className="name float-left">Cours de {specialite.name} </h2>
                    {this.props.isLoadingChapitres && 
                        <div className="float-right">
                            <Loading />
                        </div>
                    }
                </div>
                <div className="row">
                    <div className="col-12 clearfix">
                        <ProtectedButton onClick = {this.ajouterCours} title="Ajouter un cours" className="btn btn-success mb-3 float-right"/>
                    </div>
                    {this.props.chapitres.map(({id,resume,title})=>(
                        <div key={id} className="col-12">
                            <ListCoursItem id = {id} resume = {resume} title = {title} />
                        </div>                   
                    ))}
                </div>
            </div>  
        );
    }

}

const ListCoursItem = ({title, resume, id})=>(
        <div className="enseignement-card" onClick={()=>toLink(lienOuvrirChapitre({chapitreID:id,p:TAB_FICHE}))}>
            <div className="enseignement-image">
                <img src={require('../../../img/cours-default.png')} alt="Card cap" />
            </div>
            
            <div className="card-body">              
                <h4 className="card-title">{title}</h4>
                <p className="card-text">{resume} </p>
                <div className="clearfix">
                    <p className="card-text float-left">
                        20 exercices | 5 Quiz
                    </p>
                    <p className="card-text float-right">
                        Maths 6°
                    </p>
                </div>
            </div>
        
        </div>
)

export default ListCours;