import history from '../history';
import { HOME, ENSEIGNEMENT_BASE } from '../routes/liens';

export function toLink(link='/'){
    history.push(link)
}

export function toHome(){
    history.push(HOME);
}

export function getLienEnseignement(codeClasse="", codeSpecialite=null, page=null){
    let link = `${ENSEIGNEMENT_BASE}/${codeClasse}`;
    if(codeSpecialite===null){
        return link;
    }
    link = `${link}/${codeSpecialite}`;
    if(page){
        return `${link}/${page}`;
    }
    return link;
}

export function toLienEnseignement(codeClasse="", codeSpecialite=null, page=null){
    toLink(getLienEnseignement(codeClasse,codeSpecialite,page))
}