import React from 'react';
import {Link} from 'react-router-dom';
import { getLienEnseignement } from '../navigation';


const TimeLine = ({title,programme,code,name})=>{
    if(!programme){
        return null;
    }
    const {classe,specialite} = programme;
    return(
        <div className="timeline">
            <Link to={getLienEnseignement(classe.code)} className="timeline-item icon ">
                {classe.name}
            </Link>
            <Link to={getLienEnseignement(classe.code, specialite.code)} className="timeline-item icon">
                {specialite.name}
            </Link>
            <Link to={getLienEnseignement(classe.code,specialite.code,code)} className="timeline-item icon">
                {name}
            </Link>
            <div className="timeline-item active">{title}</div>
        </div>
    )
}

export default TimeLine;