import React, {Component} from 'react';

import {Link} from 'react-router-dom';

import {
    Navbar,
    Nav,
} from 'reactstrap';

import {ScrollSync, ScrollSyncPane} from 'react-scroll-sync';

import TextareaAutosize from 'react-autosize-textarea';

import TextToLatex from './TextRender';
import Loading from './Loading';

export default class Editeur extends Component {
    


    render() {
        
        return (
            <div className="container-fluid">
                <Navbar color="primary"  expand="sm" fixed="top">
                    <span className="text-white">{this.props.title}</span>
                    {this.props.isLoading && <Loading className="text-white"/>}
                    <Nav className="ml-auto" navbar>
                        <button
                            disabled = {this.props.isSaving}
                            onClick={()=>{}} 
                            className="btn btn-md btn-outline-danger mr-2" 
                            type="button">
                            Quitter
                        </button>
                        <button
                            type="button"
                            className="btn btn-md btn-primary" 
                            disabled = {this.props.isSaving}
                            onClick={this.props.onSave.bind(this)}
                        >
                            Enregistrer
                        </button>
                    </Nav>
                </Navbar>
                <ScrollSync>
                    <div className="row">
                        <ScrollSyncPane>
                            <div className="col-sm-6 col-md-6 fixarear" style={{backgroundColor: 'rgba(247, 247, 247, 0.28)'}}>
                                <TextareaAutosize
                                    id="content"
                                    name="content"
                                    rows={5}
                                    placeholder="Saisir ..."
                                    className="form-control col-12"
                                    style={{backgroundColor: 'transparent', border:0,borderRadius:0, boxShadow:"none"}}
                                    value={this.props.content}
                                    disabled={this.props.isLoading}
                                    onChange={this.props.handleContent}
                                />
                            </div>
                        </ScrollSyncPane>
                        <ScrollSyncPane>
                            <div className="col-sm-6 p-2 col-md-6 fixarear offset-md-6 offset-sm-6 d-none d-sm-block enseignement">
                                <TextToLatex html={true} text={this.props.content} />
                            </div> 
                        </ScrollSyncPane>
                    </div>
                </ScrollSync>
            </div>
        );
    }
}