import React from 'react';
import { Switch, Redirect,Route} from 'react-router-dom'

import { getLienEnseignement } from '../../navigation/index.js';
import { ENSEIGNEMENT_LIST, CODE_COURS, CODE_EXAMENS, CODE_DEVOIRS } from '../../routes/liens.js';
import ListCours from '../../pages/Cours/ListCours/ListCours';
import ListDevoirs from '../../pages/Controles/List/ListDevoirs';
import ListExamens from '../../pages/Controles/List/ListExamens';
import { isClasseExamen } from '../../utils/fonctions.js';


const LINKS = [{
        name : "Cours",
        path : CODE_COURS,
        component : ListCours
    },
    {
        name : "Devoirs",
        path : CODE_DEVOIRS,
        component : ListDevoirs
    }
];


class ListEnseignementLayout extends React.Component {
    
    constructor(props){
        super(props);
        this.state = {
            codeSpecialite : null
        }
    }
    checkSpecialite(){
        const index = this.props.classe.specialites.findIndex((s)=>(
            s.code === this.props.match.params.codeSpecialite
        ))
        return index !== -1;
    }

    componentDidMount(){
        const index = this.props.classe.specialites.findIndex((s)=>(
            s.code === this.props.match.params.codeSpecialite
        ));
        if(index !== -1){
            const specialite = this.props.classe.specialites[index];
            this.props.setSpecialite(specialite);
            this.setState({codeSpecialite : specialite.code});
        }

    }

    static getDerivedStateFromProps(nextProps, prevState){
        const {codeSpecialite} = nextProps.match.params;
        if(prevState.codeSpecialite && codeSpecialite.toString() !== prevState.codeSpecialite){

            const index = nextProps.classe.specialites.findIndex((s)=>(
                s.code === codeSpecialite
            ));

            if(index !== -1){
                nextProps.setSpecialite(nextProps.classe.specialites[index]);
            }

            return {codeSpecialite};
        }
        return null;
    }

    render() {

        if(!this.props.classe.id){
            return null;
        }
        
        // si la specialite n'est defini => redirection
        if(this.props.classe && !this.checkSpecialite()){
            return <Redirect to={getLienEnseignement(this.props.match.params.codeClasse)} />
        }

        const{type} = this.props.classe;

        // on affiche les composants
        return (
            <div className="container">
                <Switch>
                    {LINKS.map(({path, component})=><Route key={path} exact path={`${ENSEIGNEMENT_LIST}/${path}`} component={component} />)}
                    {isClasseExamen(type) && <Route exact path={`${ENSEIGNEMENT_LIST}/${CODE_EXAMENS}`} component={ListExamens} />}
                    <Redirect to={`${ENSEIGNEMENT_LIST}/${CODE_COURS}`} />
                </Switch>
            </div>
        )
    }


}

export default ListEnseignementLayout;