import React from 'react';
import ListControles from './ListControles.js';
import { CODE_EXAMENS } from '../../../routes/liens';

class ListExamens extends React.Component  {


    /**
     * Rendu de la page HTML
     */
    render(){
        return (
            <ListControles 
                codeClasse = {this.props.match.params.codeClasse}
                codeSpecialite = {this.props.match.params.codeSpecialite}
                type = {CODE_EXAMENS} />
        );
    }

}

export default ListExamens;