import React from 'react';

import {Link} from 'react-router-dom';

import ExerciceItem from './ExerciceItem';
import Loading from '../../../commun/Loading';
import { lienCreerExercice } from '../../../navigation/cours';


class ListExercice extends React.Component  {
    /**
     * Rendu de la page HTML
     */
    render(){
        return (
            <div className="exercices">
                <div className="d-flex justify-content-between align-items-center">
                    <h2 className="title">Liste des exercices</h2>
                    {this.props.loading && <Loading />}
                    <Link to={lienCreerExercice(this.props.chapitreID)} className="btn btn-success btn-sm ">
                        Ajouter un exercice
                    </Link>
                </div>
                {this.props.exercices.map((exercice,i)=>(
                    <ExerciceItem key={exercice.id} num = {i} exercice = {exercice} />
                ))}
            </div>  
        );
    }

}


export default ListExercice;