import {connect} from 'react-redux';

import VoirCours from './VoirCours.jsx';

//import {getListChapitres,isLoadingChapitres} from '../../../redux/reducers/chapitreReducer';
import { loadChapitre, toggleChapitreModal, loadExercices } from '../../../redux/actions/chapitresActions.js';

import {getFullChapitre,isLoadingChapitre, getListChapitres} from '../../../redux/reducers/chapitreReducer';

function mapStateToProps(state) {
    return {
        chapitre : getFullChapitre(state),
        chapitres : getListChapitres(state),
        isLoadingChapitre : isLoadingChapitre(state)
    };
}

function mapDispatchToProps(dispatch) {
    return {
        loadCours: function(chapitreID){
            dispatch(loadChapitre(chapitreID));
            dispatch(loadExercices(chapitreID));
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(VoirCours);
