import {connect} from 'react-redux';

//import {withRouter} from 'react-router-dom';

import {getAppClasse, getAppClasses, appClasseLoading} from '../../redux/reducers/appReducer';
import { loadAppClasse } from '../../redux/actions/appActions';

import EnseignementLayout from './EnseignementLayout.jsx';


function mapStateToProps(state) {
    return {
        classe : getAppClasse(state),
        classes : getAppClasses(state),
        appClasseLoading : appClasseLoading(state)
    };
}

function mapDispatchToProps(dispatch) {
    return {
        loadAppClasse : function(codeClasse){
            dispatch(loadAppClasse(codeClasse));
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(EnseignementLayout);
