import {connect} from 'react-redux';

//import {withRouter} from 'react-router-dom';

import {getAppClasse, getAppClasses, getAppSpecialite} from '../../redux/reducers/appReducer';

import Home from './Home';
import { loadAppClasse } from '../../redux/actions/appActions';


function mapStateToProps(state) {
    return {
        classe : getAppClasse(state),
        specialite : getAppSpecialite(state),
        classes : getAppClasses(state)
    };
}

function mapDispatchToProps(dispatch) {
    return {
        loadAppClasse : function(classeID){
            dispatch(loadAppClasse(classeID));
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Home);
